@import "../../global.scss";

.testimonials{
    background-color: rgb(43, 42, 42);
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mobile{
        justify-content: space-around;
    }

    h1{
        font-size: 50px;
        color: white;

        @include mobile{
            font-size: 20px;
        }
    }

    .container{
        width: 100%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile{
            flex-direction: column;
            height: 100%;
           
        }

        .card{
            background-color: rgb(238, 238, 238);
            width: 250px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px rgb(255, 255, 255);
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            transition: all 1s ease;

            @include mobile{
                height: 180px;
                margin: 10px 0;
                
            }

            &.featured{
                width: 300px;
                height: 75%;
                margin: 0 30px;

                @include mobile{
                    width: 250px;
                    height: 180px;
                    margin: 1px;
                }
            }

            &:hover{
                transform: scale(1.1);
            }

            .top{
                display: flex;
                align-items: center;
                justify-content: center;

                img{
                    &.left,&.right{
                        height:25px;
                    }
                    &.user{
                        height: 60px;
                        width: 60px;
                        border-radius: 50%;
                        object-fit: contain;
                        margin: 0 30px;
                        background-color: rgb(74, 77, 83);

                        @include mobile{
                            width: 30px;
                            height: 30px;
                        
                        }
                    }
                }
            }

            .center{
                padding: 10px;
                border-radius: 10px;
                background-color: crimson;
                color: white;
                font-weight: 300;
                cursor: pointer;
                transition: .3s ease;
                
                a{
                    font-size:inherit;
                    color:inherit;
                    text-decoration: none;
                }

                

                @include mobile{
                    font-size: 12px;
                    padding: 5px;
                }
            }

            .center:hover{
                background-color: rgb(216, 140, 156);
            }

            .bottom{
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                

                h3{
                    margin-bottom: 5px;
                    font-size: 12px;
                    

                    @include mobile{
                        font-size: 12px;
                    }
                }
                h4{
                    color:gray;
                    

                    @include mobile{
                        font-size: 13px;
                    }
                }

            }
        }
    }
}